import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import kotlinx.browser.window

sealed interface Route {
    val isRoot get() = this is Root

    object Root : Route
    object Unknown : Route
    data class Content(val id: ContentId) : Route
}

class Routing {
    var route: Route by mutableStateOf(location())
        private set

    init {
        window.onpopstate = {
            route = location()
            @Suppress("RedundantUnitExpression") // Make compiling
            Unit
        }
    }

    private fun location(): Route = window.location.hash.toRoute()

    fun onNavigate(id: ContentId?) {
        window.history.pushState(
            data = Unit,
            title = if (id == null) Const.title else "${Const.title}: ${id.name}",
            url = window.location.origin + id?.let { "/" + it.toUrl() }.orEmpty()
        )
        route = id.toRoute()
    }

    private fun ContentId?.toUrl(): String = if (this == null) "" else "#/$path"

    private fun String.toRoute(): Route {
        if (isEmpty()) return Route.Root
        if (!startsWith("#/")) return Route.Unknown
        val id = ContentId.fromPath(drop(2)) ?: return Route.Unknown
        if (id !in Const.published) return Route.Unknown
        return Route.Content(id)
    }

    private fun ContentId?.toRoute(): Route = if (this == null) Route.Root else Route.Content(this)
}
